      targetArr.forEach((it, i) => {
        const price = priceArr[i]
        if (it < 0) {
          if (account > perBuy * price) {
            account = account - perBuy * price
            usdtAccount = usdtAccount + perBuy
            operateTime++
            perBuyPrice = price
          }
        }
        if (it > 0) {
          if (usdtAccount > perBuy) {
            if (price < perBuyPrice) {
              return
            }
            account = account + perBuy * price
            usdtAccount = usdtAccount - perBuy
