              trigger: 'axis'
          },
          legend: {
              data:['usdt','exchange']
          },
          grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
          },
          dataZoom: [{
              type: 'inside',
              start: 90,
              end: 100
          }, {
              start: 0,
              end: 10,
              handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
              handleSize: '80%',
              handleStyle: {
                  color: '#fff',
                  shadowBlur: 3,
                  shadowColor: 'rgba(0, 0, 0, 0.6)',
                  shadowOffsetX: 2,
                  shadowOffsetY: 2
              }
          }],
          xAxis: {
              type: 'category',
              boundaryGap: false,
              data: time
          },
          yAxis: [
            {
              name: 'price',
              type: 'log',
              max: 'dataMax',
              min: 'dataMin'
            },
            {
                name: 'diff',
                max: 'dataMax',
                min: 'dataMin',
                type: 'value',
            }
          ],
          series: [
              {
                  name:'usdt',
                  type:'line',
                  data: usdtQc
              },
              {
                  name:'exchange',
                  type:'line',
                  // yAxisIndex:1,
                  data: hlKline.c.reverse()
              },
              {
                  name:'diff',
                  type:'line',
                  yAxisIndex:1,
                  data: diff
              },
          ]
      };
      const kline1 = echarts.init(document.getElementById('kline1'));
      kline1.setOption(option);
    },
    getKline(interval, symbol, size) {
//   1417536000000, 时间戳
// 2370.16, 开
// 2380, 高
// 2352, 低
// 2367.37, 收
// 17259.83 交易量
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          this.$http('http://api.zb.cn/data/v1/kline', {
            params: {
              market: symbol,
              type: interval,
              size: size || 1000
            }
          }).then(res => {
            this.kline[symbol] = res.data.data
            // return res.data.data
            resolve(res.data.data)
          })
        }, 1000);
        
      }) 
    },
    initData(interval) {
        // this.getKline(interval, "usdt_qc").then(() => {
        //   return this.getKline(interval, "btc_qc")
        // }).then(() => {
        //   return this.getKline(interval, "btc_usdt")
        // }).then(() => {
        //   return this.getKline(interval, 'eos_qc')
        // }).then(() => {
        //   return this.getKline(interval, 'eos_usdt')
        // }).then(res => {
        //   this.initKline()
        // })
        // this.getKline(interval, "usdt_qc").then(res => {
        //   this.initKline1()
        // })
        this.getKline(interval, "usdt_qc", 200).then(res => {
          this.initKline2()
        })
    }
  }
}
</script>



